<template>
  <el-scrollbar class="scrollbar">
    <div v-for="(log, index) in logMessages" :key="index">
      {{ log }}
    </div>
  </el-scrollbar>
</template>

<script>
export default {
  data() {
    return {
      logMessages: [], // 存储日志信息
      ws: null,
      reconnectAttempts: 0, // 当前重连尝试次数
      maxReconnectAttempts: 5, // 最大重连次数
      reconnectDelay: 5000, // 重连间隔 (毫秒)
      manualClose: false, // 是否为手动关闭
    };
  },
  mounted() {
    this.initWebSocket();
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
  },
  beforeUnmount() {
    this.closeWebSocket(); // 组件销毁时关闭 WebSocket
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
  },
  methods: {
    initWebSocket() {
      if (this.ws && (this.ws.readyState === WebSocket.OPEN || this.ws.readyState === WebSocket.CONNECTING)) {
        console.log('WebSocket is already connected or connecting');
        return;
      }

      this.logMessages = [];
      this.manualClose = false;
      const baseURL = this.$axios.defaults.baseURL.replace(/^http/, 'ws').replaceAll('/api', '');
      const wsURL = `${baseURL}/logs`;
      this.ws = new WebSocket(wsURL);

      this.ws.onopen = () => {
        console.log('WebSocket connected');
        this.reconnectAttempts = 0;
        this.startHeartbeat();
      };

      this.ws.onmessage = (event) => {
        const message = event.data;
        if (!this.logMessages.includes(message)) {
          this.logMessages.push(message);
        }
        if (this.logMessages.length > 30) {
          this.logMessages.shift();
        }
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error);
        this.handleReconnect();
      };

      this.ws.onclose = (event) => {
        console.warn('WebSocket closed:', event);
        this.stopHeartbeat();
        if (!this.manualClose) {
          this.handleReconnect();
        }
      };
    },
    closeWebSocket() {
      if (this.ws) {
        this.manualClose = true;
        this.stopHeartbeat();
        this.ws.close();
        this.ws = null;
      }
      this.logMessages = [];
    },
    startHeartbeat() {
      if (!this.heartbeatInterval) {
        this.heartbeatInterval = setInterval(() => {
          if (this.ws.readyState === WebSocket.OPEN) {
            this.ws.send('ping');
          }
        }, 30000);
      }
    },
    stopHeartbeat() {
      if (this.heartbeatInterval) {
        clearInterval(this.heartbeatInterval);
        this.heartbeatInterval = null;
      }
    },
    handleReconnect() {
      if (document.visibilityState === 'hidden') {
        console.warn('Page is hidden, skipping reconnect...');
        return;
      }

      if (this.reconnectAttempts >= this.maxReconnectAttempts) {
        console.error('Max reconnect attempts reached. Giving up.');
        return;
      }

      setTimeout(() => {
        this.reconnectAttempts++;
        console.log(`Reconnecting... (Attempt ${this.reconnectAttempts})`);
        this.initWebSocket();
      }, this.reconnectDelay);
    },
    handleVisibilityChange() {
      if (document.visibilityState === 'hidden') {
        console.log('Page hidden, closing WebSocket...');
        this.stopHeartbeat();
        this.closeWebSocket();
      } else if (document.visibilityState === 'visible') {
        console.log('Page visible, reconnecting WebSocket...');
        this.initWebSocket();
      }
    },
  },
};
</script>

<style scoped>
.scrollbar {
  height: 700px;
  border: 1px solid #ccc;
  padding: 10px;
}
</style>
